.content ul li:before {
  content: '';
  position: absolute;
  top: 6px;
  left: 0;
  width: 10pxpx;
  height: 10pxpx;
  border-radius: 50%;
}

.content ol {
  counter-reset: myCounter;
}

.content ol li {
  position: relative;
  padding-left: 20px;
}

.content ol li:before {
  counter-increment: myCounter;
  content: counter(myCounter) ". ";
  position: absolute;
  top: 0;
  left: 0;
}

